import {Stack, Box, Typography, Button} from '@mui/material';
import {transitionTwo, appStoreIcon, playStoreIcon, yemeksepetiLogo, getirLogo, migrosLogo} from '../../assets';
import {SecondaryColor, SecondaryColorHoverColor, ButtonSecondary} from '../../colors';
function Discover(){
    const logos = [yemeksepetiLogo, getirLogo, migrosLogo]
    return(
        <Box id="discover">
            <Box sx={{background:`url(${transitionTwo})`, backgroundSize:'cover', backgroundRepeat:'no-repeat', width:'100%', height:{xs:'200px',sm:'350px',md:'456px'}}} />
            <Stack direction={{xs:'column',md:'row'}} margin='0 5%' sx={{position:'relative'}} gap='2.5vw'>
                <Typography variant='h1' color='#fff' fontWeight='bold' fontSize={{xs:'20vw',sm:'15vw'}} sx={{textTransform:'uppercase',lineHeight:'1'}} mt={{xs:'-19vw',sm:'-14vw'}}>disco<br /><span style={{color:'rgba(0, 0, 0, 0.87)'}}>ver<br />more</span></Typography>
                <Stack>
                    <Stack direction={{xs:'column',sm:'row'}} mt={{xs:'30px',md:'75px'}} gap='25px'>
                        <Button sx={{color:'white',backgroundColor:SecondaryColor, height:'50px', '&:hover':{backgroundColor:SecondaryColorHoverColor}, boxShadow:'0 0 20px rgba(225, 171, 26,0.4)', padding:'35px 28px', borderRadius:'28px',  borderBottomLeftRadius:'0', lineHeight:'1.4'}}><img width='30' src={appStoreIcon} style={{marginRight:'5px'}}/>Available on app store</Button>
                        <Button sx={{height:'50px', color:SecondaryColor, backgroundColor:'white', padding:'35px 28px', borderRadius:'28px',  borderBottomRightRadius:'0', boxShadow:'0 0 20px rgba(0, 0, 0,0.1)', lineHeight:'1.4'}}><img style={{marginRight:'5px'}} width='30' src={playStoreIcon} />available on play store</Button>
                    </Stack>
                    <Typography fontWeight='500' mt='25px' fontSize='30px' sx={{textTransform:'capitalize'}}>You can <span style={{fontWeight:'bold', color:ButtonSecondary}}>order</span> on Android and IOS as well!</Typography>
                    <Typography variant='body2' sx={{textTransform:'capitalize'}}>Minimum Requirements<br />(Version 2.22.13.77)<br />Android OS 4.0.3 or above<br />iOS 10 or above</Typography>
                    <Typography mt='10px' sx={{color:SecondaryColorHoverColor, textTransform:'capitalize'}}>You can use the following Apps for your <span>order</span></Typography>
                </Stack>
            </Stack>
            <Stack direction='row' gap='50px' justifyContent='center' alignItems='center' m='50px 5%' flexWrap={{xs:'wrap',md:'nowrap'}}>
                {logos.map((logo, index) => {
                    return(
                        <Box sx={{cursor:'pointer',backgroundColor:'white', borderRadius:'15px', height:'100px', width:'33.33%', display:'flex', justifyContent:'center'}} key={index} p='20px'>
                            <img src={logo} style={{filter:'saturate(0)', maxWidth:'80%', height:'auto', objectFit:'contain'}} />
                        </Box>
                    )
                })}
            </Stack>
        </Box>
    )
}
export default Discover;