import {Typography} from '@mui/material';
import {useParams} from 'react-router-dom';
import {SecondaryHover} from '../colors';
function NoMatch(){
    const param = useParams();
    let valueParam = param[Object.keys(param)[0]];
    const {pizzaId, nbPizzaSelected, sizePizzaSelected} = useParams();
    if(valueParam.toLowerCase() == 'pizzatypes' && (!pizzaId||!nbPizzaSelected||!sizePizzaSelected)){
        return(
            <Typography m='auto 10%' variant="h5" sx={{textAlign:'center', color:SecondaryHover, fontWeight:'bold', textTransform:'capitalize'}}>You should select the pizza. Go to the main page and click on discover in the pizza cards.</Typography>
        )
    }
    
}
export default NoMatch;