import {useState} from 'react';
import { Link } from 'react-router-dom';
import {Box, Stack, Typography, Button, Card, CardContent} from '@mui/material';
import { styled } from '@mui/system';
import {SecondaryHover, ButtonSecondary, ColorLink} from '../colors';
import ButtonPizzaOptions from './buttonPizzaOptions';
const CardStyled = styled(Card)({
    width:'40%',
    borderRadius:'30px',
    padding:'0 20px 5px',
    overflow:'visible',
    '& .MuiCardContent-root':{
        paddingTop:'50px',
    }
});
function CardPizza({cardInfo}){
    const [cardSizePizza, setCardSizePizza] = useState('m');
    const [cardNumberPizza, setCardNumberPizza] = useState(1);
    const shareNumberPizza = (value) => {
        setCardNumberPizza(value)
    }
    const shareSizePizza = (value) => {
        setCardSizePizza(value)
    }
    const stylePrice = {
        color:ButtonSecondary,
        fontSize:'15px',
        fontWeight:'500'
    }
    const bottomDesignCard = {
        backgroundColor:'#F1F1F1', 
        width:'50px',
        height:'17px', 
        border:'0.5px solid #cac7c7', 
        borderRadius:'50px'
    }
    return(
            <CardStyled variant='outlined' sx={{width:{xs:'100%',sm:'400px'}}}>
                <CardContent sx={{display:'flex', flexDirection:'column',alignItems:'center', position:'relative'}}>
                    <Box 
                    sx={{
                        backgroundImage:`url(${cardInfo.imageUrl})`, backgroundRepeat:'none', backgroundSize:'cover', backgroundPosition:'right',width:'80px', height:'80px', borderRadius:'100%', border:'7px solid white', position:'absolute', top:0,left:'50%', transform:'translate(-50%,-50%)', zIndex:9
                    }} 
                    />
                    <Typography variant='h5' fontWeight='bold'>{cardInfo.nameChef}</Typography>
                    <Typography sx={{color:ColorLink, fontSize:'10px'}}>{cardInfo.email}</Typography>
                    <Typography sx={{fontSize:'14px', textAlign:'left'}} mb={2} mt={1}>{cardInfo.desc}</Typography>

                    <Link to={`/pizzaTypes/${cardInfo.pizzaId}/${cardNumberPizza}/${cardSizePizza}`}>
                        <Button 
                            sx={{
                            backgroundColor:ButtonSecondary, marginBottom:'16px', width:'150px',color:'white','&:hover':{backgroundColor:SecondaryHover}
                            }}>
                                Discover
                        </Button>
                    </Link>
                    <Stack direction={{xs:'column',sm:'row'}} gap={{xs:'16px',sm:''}} align='left' justifyContent='space-between' sx={{width:'100%'}}>
                        <Box>
                            {cardInfo.prices.map((price, index) => {
                                return(
                                    <Typography key={index} color='text.primary'>{price.title}: <span style={stylePrice}>{price.value}$</span></Typography>
                                )
                            })}
                        </Box>
                        <Box>
                            <Typography sx={{width:{sm:'160px'}}}><span style={{color:ButtonSecondary, fontWeight:'500'}}>Ingredients: </span>
                            <span style={{fontSize:'15px'}}>{cardInfo.ingredients.join(', ')}</span></Typography>
                        </Box>
                    </Stack>
                    <Stack>
                        <ButtonPizzaOptions 
                        styles={{
                            align:'center',
                            display:true
                        }}
                        properties={{
                            sizes:['s','m','l'],
                            sizeUnitLetter:'m' ,
                            pizzaId:cardInfo.pizzaId,
                            eachIndex:0
                        }}
                        shareSizePizza={shareSizePizza}
                        shareNumberPizza={shareNumberPizza}
                        />
                    </Stack>
                    <Stack mt={5} direction='row' gap='25px'>
                        <Box sx={bottomDesignCard}></Box>
                        <Box sx={bottomDesignCard}></Box>
                    </Stack>
                </CardContent>
            </CardStyled>
    )
}
export default CardPizza;