import {useState, useEffect, useContext} from 'react';
import {Stack, Button, TextField, IconButton, Typography} from '@mui/material';
import { styled } from '@mui/system';
import {ButtonSecondary} from '../colors';
import PizzaIdCartContext from '../pizzaIdCartContext';
import FavoriteIcon from '@mui/icons-material/Favorite';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import {dataPizza} from '../data';
const StyledTextField = styled(TextField)({
    width:'50px',
    "& .MuiInputBase-input":{
        color:'black',
        textAlign:'center'
    },
    '& .MuiOutlinedInput-input:focus':{
        border:'none'
    }
});
function filterSize(size){
    'use strict';
    if(/large/i.test(size)){
        return 'l';
    } else if(/small/i.test(size)){
        return 's';
    } else if(/medium/i.test(size)){
        return 'm';
    }else{
        return size;
    }
}
function ButtonPizzaOptions({properties, shareSizePizza, shareNumberPizza, styles}){
    const styleBtnsNub = {
        backgroundColor:'#e5e5e5',
        color:'text.primary',
        padding:'5px 15px',
        minWidth:0,
        boxShadow:properties.shadow ? '0 0 10px #b8b8b8' : 'none',
        '&:hover':{
            backgroundColor:'#dbdbdb'
        }
    }
    const [numberOfPizza, setNumberOfPizza] = useState(properties.initialNumberOfPizza || 1);
    const [sizeOfPizza, setSizeOfPizza] = useState(properties.sizeUnitLetter);
    const [price, setPrice] = useState(0);
    const pizzaSharedToCart = useContext(PizzaIdCartContext);
    function makeid(length) {
        let result = '';
        const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
        const charactersLength = characters.length;
        let counter = 0;
        while (counter < length) {
          result += characters.charAt(Math.floor(Math.random() * charactersLength));
          counter += 1;
        }
        return result;
    }
    let uniqueId = makeid(7);
    const saveToCart = () => {
        pizzaSharedToCart.setPizzaCart([{pizzaId:properties.pizzaId,keyId:uniqueId,numberPizza:numberOfPizza, sizePizza:sizeOfPizza}, ...pizzaSharedToCart.pizzaCart]);
    }
    const addPizza = (event) => {
        if(numberOfPizza == 10){
            event.preventDefault();
            return '';
        }
        setNumberOfPizza(prev => prev + 1);
    }
    const removePizza = (event) => {
        if(numberOfPizza == 1){
            event.preventDefault();
            return '';
        }
        setNumberOfPizza(prev => prev - 1);
    }
    useEffect(()=>{
        shareNumberPizza(numberOfPizza);
        setSizeOfPizza(sizeOfPizza);
        if(!styles.display){
            const objPizza = dataPizza.filter((single) => {
                return single.pizzaId == properties.pizzaId;
            })[0];
            const origPrice = objPizza.price.filter((singlobjpr) => {
                return singlobjpr.unit == sizeOfPizza;
            })[0].value;
            setPrice(origPrice * numberOfPizza);
            let array = pizzaSharedToCart.pizzaCart;
            array[properties.eachIndex].numberPizza = numberOfPizza;
            array[properties.eachIndex].sizePizza = sizeOfPizza;
            pizzaSharedToCart.setPizzaCart(array)
        }
    }, [sizeOfPizza, numberOfPizza])
    return(
    <Stack alignItems={styles.align} mb={properties.mb}>
        {!styles.display ? <Typography>{price.toFixed(2)}$</Typography> :''}
        <Stack mb={properties.mb} direction='row' alignItems='center' mt={2} gap='15px'>
            <StyledTextField sx={{boxShadow:properties.shadow ? '0 0 10px #b8b8b8' : 'none'}} readOnly={true} type='text' variant='outlined' size='small' value={numberOfPizza}/>
            <Button sx={styleBtnsNub} onClick={addPizza}>+</Button>
            <Button sx={styleBtnsNub} onClick={removePizza}>-</Button>
        </Stack>
        <Stack mb={properties.mb}  direction='row' alignItems='center' mt={2} gap='15px'>
            {
                properties.sizes.map((size) => {
                    return(
                        <Button 
                        key={size} 
                        onClick={() => {
                            console.log(size, filterSize(size));
                            setSizeOfPizza(filterSize(size));
                            shareSizePizza(filterSize(size))
                        }} 
                        sx={styleBtnsNub} 
                        style={{
                            backgroundColor:filterSize(size) == sizeOfPizza ? ButtonSecondary : '#e5e5e5',
                            color: filterSize(size) == sizeOfPizza ?  '#fff' : 'inherit'
                        }}
                        >{size}</Button>
                    )
                })
            }
        </Stack>
        {
            styles.display ? 
            <Stack mb={properties.mb} direction='row' justifyContent='center' alignItems='center' mt={2} gap='15px'>
                <IconButton onClick={saveToCart} sx={styleBtnsNub} style={{padding:'10px'}}><ShoppingCartIcon /></IconButton>
                <IconButton sx={styleBtnsNub} style={{padding:'10px'}}><FavoriteIcon /></IconButton>
            </Stack> 
            :
            ''
        }
    </Stack>
    )
}
export default ButtonPizzaOptions;