import {useState, useEffect} from 'react';
import {Link} from 'react-router-dom';
import {Box, Typography, Stack, Button} from '@mui/material';
import CircleIcon from '@mui/icons-material/Circle';
import ButtonPizzaOptions from '../../components/buttonPizzaOptions';
import {SecondaryColor, HoverPrimary, SecondaryHover} from '../../colors';
function Header({properties, selectedPizzaData}){
    const [mainImage, setMainImage] = useState(selectedPizzaData.mainImage);
    const [images, setImages] = useState(selectedPizzaData.images);
    const [nb, setNb] = useState(properties.nbPizzaSelected);
    const [sz, setSZ] = useState(properties.sizePizzaSelected);
    const priceForSize  = selectedPizzaData.price.filter((singlePrice) =>{
        return singlePrice.unit == sz;
    })[0];
    const [price, setPrice] = useState(priceForSize.value * nb);
    useEffect(()=>{
        setPrice(priceForSize.value * nb);
    },[nb,sz]);
    const changePic = (imgClicked, index) => {
        let arr = images;
        arr[index] = mainImage;
        setMainImage(imgClicked);
        setImages(arr);
    }
    return(<Stack m='100px 10% 0' gap={{xs:'50px',md:'100px'}} direction={{sm:'column',md:'row'}} justifyContent='space-around'>
        <Stack flex={1} gap='50px' alignItems='center'>
            <Box sx={{width:{xs:'300px',sm:'450px'}}}>
                {selectedPizzaData ? <img src={mainImage} style={{maxWidth:'100%'}}/> : 'loading'}
                {/* <img src={mainImage} style={{maxWidth:'100%'}}/> */}
            </Box>
            <Stack direction='row' gap='10px'>
                {
                    images.map((img, index) => {
                        return(
                        <Button key={index} onClick={() => {changePic(img, index)}} sx={{backgroundColor:'white', borderRadius:'10px', boxShadow:'0 0 10px #b8b8b8'}}>
                            <img src={img} style={{maxWidth:'100%'}}/>
                        </Button>)
                    })
                }
            </Stack>
        </Stack>
        <Stack flex={1}>
            {
                selectedPizzaData.state ? <Typography sx={{color:SecondaryColor, display:'flex', alignItems:'center', textTransform:'uppercase', fontWeight:'bold'}}><CircleIcon sx={{fontSize:'15px'}} /> {selectedPizzaData.state}</Typography> : 'loading'
            }
            {/* <Typography sx={{color:SecondaryColor, display:'flex', alignItems:'center', textTransform:'uppercase', fontWeight:'bold'}}><CircleIcon sx={{fontSize:'15px'}} /> {selectedPizzaData.state}</Typography> */}
            <ButtonPizzaOptions 
                styles={{
                    align:'start',
                    display:true
                }}
                properties={{
                    sizes:['small','medium','large'],
                    initialNumberOfPizza:parseInt(properties.nbPizzaSelected),
                    sizeUnitLetter:properties.sizePizzaSelected,
                    pizzaId:properties.pizzaId,
                    eachIndex:0,
                    shadow:true,
                    mb:'15px'
                }}
                shareNumberPizza={(val)=>{setNb(val)}}
                shareSizePizza={(val)=>{setSZ(val)}}
            />
            <Typography variant='h4' mb='15px'>Price: {price.toFixed(2)}$</Typography>
            <Link to='/checkOut'>
                <Button sx={{mb:'15px',p:'10px 0',transition:'.3s ease-in-out',width:'150px', backgroundColor:'text.primary',color:'white','&:hover':{backgroundColor:HoverPrimary}}}>Check out</Button>
            </Link>
            <a link='#' style={{textDecoration:'underline'}}>Find a store</a>
            <Typography mt='15px' sx={{color:SecondaryHover}}>
                <CircleIcon sx={{fontSize:'15px'}} /> About this pizza
            </Typography>
        </Stack>
    </Stack>)
}
export default Header;