import {useState} from 'react';
import {BrowserRouter, Routes, Route} from 'react-router-dom';
import './App.css';
import { Box, Stack } from '@mui/material';
import {Home, PizzaTypes, CheckOut} from './elements';
import Navbar from './components/navbar';
import Footer from './components/footer';
import PizzaIdCartContext from './pizzaIdCartContext';
import NoMatch from './components/noMatch';
function App() {
  const [pizzaCart, setPizzaCart] = useState([]);
  return (
  <PizzaIdCartContext.Provider value={{pizzaCart, setPizzaCart}}>
    <BrowserRouter>
          <Stack pt='1px' sx={{minHeight:'100vh'}}>
          <Navbar />
          <Routes>
              <Route path='/' exact element={<Home />} />
              <Route path='/pizzaTypes/:pizzaId/:nbPizzaSelected/:sizePizzaSelected' exact element={<PizzaTypes />} />
              <Route path='/checkOut' exact element={<CheckOut />} />
              <Route path='*' element={<NoMatch />} />
          </Routes>
          <Footer />
        </Stack>
      </BrowserRouter>
    </PizzaIdCartContext.Provider>
  );
}
export {App, PizzaIdCartContext};