import { useContext } from 'react';
import { Link } from 'react-router-dom';
import {Box, Stack, Typography, IconButton, Button} from '@mui/material';
import CloseRoundedIcon from '@mui/icons-material/CloseRounded';
import ButtonPizzaOptions from './buttonPizzaOptions';
import { HoverPrimary } from '../colors';
import PizzaIdCartContext from '../pizzaIdCartContext';
import {dataPizza} from '../data';
function CartAll({display, changeDisplay}){
    const pizzaSharedToCart = useContext(PizzaIdCartContext);
    const hideCart = () => {
        changeDisplay(false);
    }
    return(
    <Box 
        sx={{
            zIndex:'999',
            position:'fixed',
            padding:'20px',
            width:{md:'350px'},
            backgroundColor:'white',
            borderRadius:'5px',
            top:'70px',
            right:{xs:'0',md:'10%'},
            left:{xs:'0',md:'auto'},
            border:'2px solid black',
            display:display,
            overflowY:'auto',
            maxHeight:'70vh',
            margin:{xs:'0 5% !important',md:'0 !important'}
        }}>
        <Stack className="closeBtnCart" alignItems='flex-end'>
            <IconButton onClick={hideCart}>
                <CloseRoundedIcon sx={{fontSize:'30px', color:'text.primary'}} />
            </IconButton>
        </Stack>
        {
            pizzaSharedToCart.pizzaCart.map((pizzaCartSingleShared, index)=>{
                const pizzaObjSelected = dataPizza.filter((arrToCheck) => {
                    return arrToCheck.pizzaId === pizzaCartSingleShared.pizzaId
                })[0];
                const pizzaPriceSelected = pizzaObjSelected.price.filter((singlePriceToCheck)=> {
                    return singlePriceToCheck.unit == pizzaCartSingleShared.sizePizza
                })[0].value;
                return(
                    <Stack key={pizzaCartSingleShared.keyId}>
                        <Box>
                            <img />
                        </Box>
                        <Stack>
                            <Typography sx={{textTransform:'capitalize'}}>
                                {pizzaObjSelected.pizzaName}
                            </Typography>
                            <Stack direction="row" justifyContent='space-between'>
                                <ButtonPizzaOptions
                                    styles={{
                                        align:'start',
                                        display:false
                                    }}
                                    properties={{
                                        sizes:['s','m','l'],
                                        initialNumberOfPizza:pizzaCartSingleShared.numberPizza,
                                        sizeUnitLetter:pizzaCartSingleShared.sizePizza,
                                        pizzaId:pizzaCartSingleShared.pizzaId,
                                        eachIndex:index
                                    }}
                                    shareNumberPizza={(val)=>{}}
                                    shareSizePizza={(val)=>{}}
                                />
                                <Link>
                                    <Button 
                                        onClick={() => {
                                        pizzaSharedToCart.setPizzaCart(pizzaSharedToCart.pizzaCart.filter((el, i)=>{
                                            return i != index;
                                        }))}}
                                        sx={{backgroundColor:'rgba(222, 182, 117,0.6)', color:'text.primary',"&:hover":{backgroundColor:'rgba(222, 182, 117,0.8)'}}}
                                        >Cancel</Button>
                                </Link>
                            </Stack>
                        </Stack>
                    </Stack>
                )
            })
        }
        <Link to='/checkOut'><Button 
            sx={{marginTop:'20px',transition:'.3s ease-in-out',width:'100%', backgroundColor:'text.primary',color:'white','&:hover':{backgroundColor:HoverPrimary}}} 
            onClick={hideCart}>Check out</Button></Link>
    </Box>)
}
export default CartAll;