import {Stack, Typography, IconButton} from '@mui/material';
import InstagramIcon from '@mui/icons-material/Instagram';
import FacebookIcon from '@mui/icons-material/Facebook';
import TwitterIcon from '@mui/icons-material/Twitter';
import { SecondaryColor } from '../colors';
function Footer(){
    return (
        <Stack p='50px 5%' direction={{xs:'column',md:'row'}} gap='25px' justifyContent='space-between' alignItems='center' mt='auto' sx={{minHeight:'50px',backgroundColor:'#0D1114', color:'white', position:'relative', "&:before":{content:'""',position:'absolute',top:'0',transform:'translate(-50%,-50%)',left:'50%',width:'50px',height:'50px', borderRadius:'100%', backgroundColor:'#F1F1F1'}}}>
            <Stack alignItems='center'>
                <Typography variant='h4' sx={{color:SecondaryColor, textTransform:'uppercase', fontWeight:'bold'}}>Foodo</Typography>
                <Typography sx={{textTransform:"uppercase", fontSize:'13px', fotnWeight:'600'}}>Enjoy your food</Typography>
            </Stack>
            <Typography sx={{textTransform:'capitalize', color:'#787878', textAlign:'center'}}>Copyright &copy; 2023 <br />Designed and coded by Mohamed yahya mansouri - react project</Typography>
            <Stack direction='row' gap='15px'>
                <IconButton sx={{backgroundColor:'#474747', "&:hover":{backgroundColor:'#2a2a2a'}}}><InstagramIcon sx={{color:'white'}} /></IconButton>
                <IconButton sx={{backgroundColor:'#474747', "&:hover":{backgroundColor:'#2a2a2a'}}}><FacebookIcon sx={{color:'white'}} /></IconButton>
                <IconButton sx={{backgroundColor:'#474747', "&:hover":{backgroundColor:'#2a2a2a'}}}><TwitterIcon sx={{color:'white'}} /></IconButton>
            </Stack>
        </Stack>
    )
}
export default Footer;