import {Box, Stack, Typography} from '@mui/material';
import LocalDiningIcon from '@mui/icons-material/LocalDining';
import LocalPizzaIcon from '@mui/icons-material/LocalPizza';
import TableRestaurantIcon from '@mui/icons-material/TableRestaurant';
import { moreOptionsBG } from '../../assets';
import {SecondaryColor} from '../../colors';
import { styled } from '@mui/styles';
function MoreOptions(){
    const icons = [
        {icon:({style}) => {return(<LocalDiningIcon sx={style} />)}, title:'be a chef'},
        {icon:({style}) => {return(<LocalPizzaIcon sx={style} />)}, title:'our menu'},
        {icon:({style}) => {return(<TableRestaurantIcon sx={style}/>)}, title:'our locations'}
    ]
    const LineUsStyle = styled('span')({
        display:'inline-block',
        position:'relative',
        fontSize:'5rem',
        color:SecondaryColor,
        '&::before':{
            content:'""',
            width:'100%',
            height:'10px',
            left:0,
            backgroundColor:'#0D1114', 
            position:'absolute', 
            top:'52%',
        },
     });
     const reviews = [
        {name:'john doe', content:'he inbox Pizza fish pepraoni to teaching and do the doing with Salami'},
        {name:'john doe', content:'he inbox Pizza fish pepraoni to teaching and do the doing with Salami'},
        {name:'john doe', content:'he inbox Pizza fish pepraoni to teaching and do the doing with Salami'},
     ]
    return(<Box id="services" sx={{backgroundImage:`url(${moreOptionsBG})`, backgroundSize:'cover', backgroundRepeat:'no-repeat', color:'white', minHeight:'745px', backgroundPosition:{xs:'center',md:''}}} p={{xs:'10%',md:'70px 5%'}} pr={{md:'150px'}} mb='100px'>
        <Stack gap={{xs:'50px',md:'20px'}} direction={{md:'row'}} justifyContent='space-between' mb={{xs:'50px',md:'100px'}} alignItems={{md:"center"}}>
            <Box flex='1'>
                <Typography variant='h3' sx={{color:SecondaryColor}} fontWeight='bold'>Foodo</Typography>
                <Typography sx={{maxWidth:'450px'}}>The inbox Pizza fish pepraoni to teaching and do the doing with Salami and cheese, Peynir doing all the oven hot. The quick brows fro jumps as well for the main sea overeach</Typography>
            </Box>
            <Stack gap='50px' direction='row' flex='1' color={SecondaryColor} justifyContent='space-between'>
                {icons.map((service, index) => {
                    const IconTo = service.icon;
                    return(
                        <Stack key={index} alignItems='center' sx={{cursor:'pointer',transition:'transform .2s ease-in-out','&:hover':{transform:'scale(1.09)'}}}>
                            <IconTo style={{fontSize:{xs:'35px',md:'40px'}}}/>
                            <Typography fontSize={{md:'18px'}} align='center' sx={{textTransform:'capitalize'}}>{service.title}</Typography>
                        </Stack>
                    )
                })}
            </Stack>
        </Stack>
        <Stack gap='50px' direction={{md:"row" }}justifyContent='space-between'>
            <Stack>
                <Box sx={{height:'10px',background:'white', borderRadius:'2px'}}></Box>
                <Typography variant='h2' fontWeight='bold' sx={{textTransform:'uppercase', direction:{md:'rtl'}, lineHeight:1}}>what people <br />say about <br /><LineUsStyle>us</LineUsStyle></Typography>
            </Stack>
            <Box sx={{height:'300px', overflowY:'scroll', flexBasis:'400px', pr:{xs:'15px',md:'20px'}}}>
                {reviews.map((review, index) => {
                    return(
                        <Stack justifyContent='center' alignItems='center' key={index}>
                            <Typography>{review.content}</Typography>
                            <Typography fontWeight='light' mt='10px' sx={{textTransform:'capitalize', fontStyle:'italic'}}>{review.name}</Typography>
                            <Box m='20px 0' sx={{backgroundColor:SecondaryColor, width:'40%', height:'5px', borderRadius:'2px'}}></Box>
                        </Stack>
                    )
                })}
            </Box>
        </Stack>
    </Box>)
}
export default MoreOptions;