import {useParams} from 'react-router-dom';
import {Box, Typography} from '@mui/material';
import AlsoLike from './pizzaTypes/AlsoLike';
import Header from './pizzaTypes/Header';
import TransitionElement from '../components/transitionElement';
import { dataPizza } from '../data';
import {SecondaryHover} from '../colors';
function PizzaTypes(){
    const {pizzaId, nbPizzaSelected, sizePizzaSelected} = useParams();
    const selectedPizzaData = dataPizza.filter((singlePizza) => {
        return singlePizza.pizzaId === pizzaId;
    })[0];
    if(!selectedPizzaData || nbPizzaSelected > 10 || ['s','m','l'].indexOf(sizePizzaSelected.toLowerCase()) == -1){
        return (<Typography mt='auto' variant="h5" sx={{textAlign:'center', color:SecondaryHover, fontWeight:'bold'}}>Invalid Data...</Typography>);
    }
    return(
            <Box>
                {dataPizza ?'there' : 'loading'}
                <Header 
                    properties={{pizzaId: pizzaId, nbPizzaSelected: nbPizzaSelected, sizePizzaSelected: sizePizzaSelected}}
                    selectedPizzaData={selectedPizzaData}
                    />
                <TransitionElement text='you might also like' />
                <AlsoLike data={dataPizza} />
            </Box>
    )
}
export default PizzaTypes;