import {useState, useContext} from 'react';
import {Stack, Box, Typography, Tooltip} from '@mui/material';
import FavoriteIcon from '@mui/icons-material/Favorite';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import MenuIcon from '@mui/icons-material/Menu';
import { Link, useLocation } from 'react-router-dom';
import { IconButton } from '@mui/material';
import PizzaIdCartContext from '../pizzaIdCartContext'
import CartAll from './cartAll';
function Navbar(){
    const [navVisible, setNavVisible] = useState(false);
    const [openCart, setOpenCart] = useState(false);
    const lengthPizzaAddedCart = useContext(PizzaIdCartContext);
    let {pathname} = useLocation();
    let categories = [
        {title:'home', to:''},
        {title:'Best seller', to:'pizzaTypes'},
        {title:'services',to:'services'},
        {title:'contact',to:'contact'}
    ];
    return(<Stack direction='row' alignItems='center' padding='0 10%' justifyContent={{xs:'space-between', md:'space-evenly'}} mt='10px'>
        <Box>
        <Link to='/'><Typography color='#E1AB1A' fontWeight='bold' variant='h1' sx={{fontSize:'24px'}}>Foodo</Typography></Link>
        </Box>
        <Stack spacing={{xs:4,md:7}} backgroundColor={{xs:'white', md:'transparent'}} direction={{xs:'column',sm:'row'}} alignItems={{xs:'flex-start',sm:'center'}} justifyContent={{xs:'flex-start',md:'center'}} p={{xs:'18px',md:'0'}} pl='60px' position={{xs:'absolute',md:'relative'}} sx={{width:'100%',left:0,top:0, boxShadow:{xs:'0 0 16px #d1d1d1',md:'none'}, zIndex:'10', transition:'transform .3s ease-in-out',transform:{xs:navVisible ? 'translateY(0)' : 'translateY(calc(-100% - 16px))', md:'translateY(0)'}}}>
            {categories.map((category) => {
                return(
                    <Link onClick={() => {setNavVisible(false)}} key={category.to} to={`/${category.to}`}><Typography className='navbar__navitems-item' color='#050606' sx={{position:'relative', textTransform:'capitalize','&:after':{display:'none', left:{xs:'0',sm:'50%'}, transform:{xs:'translate(0)', sm:'translateX(-50%)'}}}}>{category.title}</Typography></Link>
                )
            })}
        </Stack>
        <Stack direction='row' spacing={2} position='relative'>
            <IconButton sx={{zIndex:9999, transition:'all .3s ease-in-out', position:'relative', right: navVisible ? '-90%' : '0'}} onClick={() => setNavVisible(prev => !prev)}><MenuIcon sx={{color:'#050606', display:{sm:'block',md:'none'}}}/></IconButton>
            <Tooltip title='0 Saved'>
                <IconButton><FavoriteIcon sx={{color:'#050606'}}/></IconButton>
                </Tooltip>
            <Tooltip title={`${lengthPizzaAddedCart.pizzaCart.length} Orders In Cart`}>
                <IconButton onClick={() => {setOpenCart(prev => !prev)}}><ShoppingCartIcon sx={{color:'#050606'}}/></IconButton>
            </Tooltip>
                <CartAll display={openCart ? 'block' : 'none'} changeDisplay={(displayChanged)=>{setOpenCart(displayChanged)}}/>
        </Stack>
    </Stack>)
}
export default Navbar;