import {Box, Stack, Typography} from '@mui/material';
import ButtonPizzaOptions from '../../components/buttonPizzaOptions';
import {SecondaryHover} from '../../colors';
function Card({dataCard}){
    return(
        <Stack backgroundColor='white' sx={{boxShadow:'0 10px 13px #dbdbdb', borderRadius:'5px'}} p='10px 50px'>
            <Box sx={{alignSelf:'center', width:'200px'}}>
                <img src={dataCard.mainImage} style={{maxWidth:'100%'}} />
            </Box>
            <Typography sx={{color:SecondaryHover, fontWeight:'bold',textTransform:'capitalize','&:before':{}}}>{dataCard.state}</Typography>
            <Stack direction="row" justifyContent='space-between' gap='25px' alignItems='end'>
                <ButtonPizzaOptions 
                    styles={{
                        align:'start',
                        display:true
                    }}
                    properties={{
                        sizes:['s','m','l'],
                        initialNumberOfPizza:1,
                        sizeUnitLetter:'m',
                        pizzaId:dataCard.pizzaId,
                        eachIndex:0,
                        shadow:false,
                        mb:'0'
                    }}
                    shareNumberPizza={(val)=>{}}
                    shareSizePizza={(val)=>{}}
                />
                <Typography>Price: 5.99$</Typography>
            </Stack>
        </Stack>
    )
}
function AlsoLike({data}){
    return(
    <Stack pb='20px' direction='row' gap='50px' sx={{width:'80%', margin:'100px auto',overflowX:'auto'}} position='relative'>
        {
            data.map((singleRecCard, index) => {
                return(
                    <Card key={index} dataCard={singleRecCard}/>
                )
            })
        }
    </Stack>
    )
}
export default AlsoLike;