import {Stack, Box, Typography} from '@mui/material';
import {SecondaryColor} from '../colors';
function TransitionElement({text}){
    return(
        <Stack sx={{marginTop:'60px', backgroundColor:SecondaryColor}} alignItems='center' justifyContent='center' p='30px'>
            <Typography variant='h4' sx={{color:'white', textTransform:'uppercase', fontSize:{xs:'20px',sm:'30px',md:'40px'}}} mb='5px' fontWeight='bold'>{text}</Typography>
            <Box sx={{width:{xs:'50px',sm:'100px',md:'150px'},height:'7px', backgroundColor:'white', borderRadius:'15px'}}></Box>
        </Stack>
    )
}
export default TransitionElement;