import {imageAvatarOne, imageAvatarTwo, pizzaSecond, firstPizzaCard, loveAtFirstBiteMobile, loveAtFirstBite} from '../../assets';
import {Stack, Box, Typography, Button} from '@mui/material';
import CardPizza from '../../components/cardPizza'; 
import {dataPizza} from '../../data';
function BestSeller(){
    const iFirstCart = 0;
    const iSecondCart = 1;
    return(<Box mt='100px' id="bestSeller">
            <Stack gap='25px' sx={{margin:'0 10%', position:'relative', zIndex:'2'}} direction={{sm:'column',md:'row'}} justifyContent={'space-around'} alignItems='center'>
                <CardPizza cardInfo={{
                    pizzaId:dataPizza[iFirstCart].pizzaId,
                    imageUrl:imageAvatarOne,
                    nameChef:dataPizza[iFirstCart].nameChef,
                    email:dataPizza[iFirstCart].email,
                    desc:dataPizza[iFirstCart].desc,
                    prices:dataPizza[iFirstCart].price,
                    ingredients:dataPizza[iFirstCart].ingredients,

                }}/>
                <Box><img alt='pizza' src={firstPizzaCard} style={{maxWidth:'90%', objectFit:'contain'}} /></Box>
            </Stack>
            <Box sx={{backgroundImage:{xs:`url(${loveAtFirstBiteMobile})`, sm:`url(${loveAtFirstBite})`}, backgroundSize:{xs:'cover',sm:'contain'}, backgroundRepeat:'no-repeat', backgroundPosition:'center',width:'100vw', height:'748px', marginTop:{xs:'-600px',sm:'-350px'}}} />
            <Stack gap={{xs:'100px',md:'0'}} sx={{margin:'10% 10% 0', position:'relative', zIndex:'2'}} direction={{sm:'column',md:'row'}} justifyContent='space-around' alignItems='center'>
                <Box><img alt='pizza' src={pizzaSecond} style={{maxWidth:'100%', objectFit:'contain', transform:'rotate(-10deg)'}} /></Box>
                <CardPizza cardInfo={{
                    pizzaId:dataPizza[iFirstCart].pizzaId,
                    imageUrl:imageAvatarOne,
                    nameChef:dataPizza[iFirstCart].nameChef,
                    email:dataPizza[iFirstCart].email,
                    desc:dataPizza[iFirstCart].desc,
                    prices:dataPizza[iFirstCart].price,
                    ingredients:dataPizza[iFirstCart].ingredients,

                }}/>
            </Stack>
        </Box>
    )
}
export default BestSeller; 