import {Typography, Box, Stack} from '@mui/material';
import {NavigateNextIcon, NavigateBeforeIcon} from '@mui/icons-material';
import {useState} from 'react';
import Button from '@mui/material/Button';
import {SecondaryColor, HoverPrimary} from '../../colors';
import {Slider} from '..';
function Header(){ 
    return(
        <Stack sx={{margin:'70px 10% 0'}} direction={{sx:'column',md:'row'}}>
            <Box flex='1' mr='10px' sx={{display:{xs:'flex',md:'block'}, flexDirection:'column'}}>
                <Typography color='text.primary' sx={{textTransform:'uppercase', fontWeight:'bold', marginBottom:0}}>total food sustainability</Typography>
                <Typography variant='h1' color={SecondaryColor} sx={{textTransform:'capitalize', fontWeight:'bold',fontSize:'40px'}} mb='5px'>satisfy your hunger</Typography>
                <Typography color='text.primary' sx={{maxWidth:'460px'}} mb='1rem'>The quick brows fro jumps as well for the main sea overeach pizza to make it delicious long for doing cooking pizza.</Typography>
                <Button variant='contained' sx={{boxShadow:'none',backgroundColor:'text.primary', color:'white', '&:hover':{background:HoverPrimary}, textTransform:'uppercase', padding:'10px 30px'}}>discover our cooks</Button>
            </Box>
            <Box flex='1.2' ml={{xs:'0',md:'10px'}} mt={{xs:'30px',md:'0'}}>
                <Slider />
            </Box>
        </Stack>
    )
}
export default Header;