import {useState} from 'react';
import {Box, Stack, Typography, Button} from '@mui/material';
import NavigateBeforeIcon from '@mui/icons-material/NavigateBefore';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import CircleIcon from '@mui/icons-material/Circle';
import CircleOutlinedIcon from '@mui/icons-material/CircleOutlined';
import { IconButton } from '@mui/material';
import {HoverPrimary, ButtonSecondary, SecondaryHover, SecondaryColor} from '../../colors';
import {imageOne, imageTwo, imageThree} from '../../assets';
function Slider(){
    const slidesInfo = [
        {url:imageOne, price:'5.49$',description:'The quick brown for pizza cooking fluffy pistache', active:true},
        {url:imageTwo,price:'6.50$',description:'Pizza Donald Cheese Try So Good manifest vegetable'},
        {url:imageThree,price:'5.50$',description:'The quick brown for pizza cooking fluffy pistache'},
    ];
    const [activeIndex, setActiveIndex] = useState(0);
    const nextSlide = () => {
        if(activeIndex >= slidesInfo.length - 1){
            setActiveIndex(0);
        }else{
            setActiveIndex(prev => prev + 1);
        }
    }
    const prevSlide = () => {
        if(activeIndex <= 0){
            setActiveIndex(slidesInfo.length - 1)
        }else{
            setActiveIndex(prev => prev - 1)
        }
    }
    const navigationBtnStyle = {
        position:'absolute',
        top:'50%',
        transform:'translateY(-50%)',
        backgroundColor:'black',
        '&:hover':{
            backgroundColor:HoverPrimary
        }
    }
    return(
    <Stack direction='column' sx={{margin:'auto',position:'relative', width:{xs:'300px',sm:'400px',md:'450px'},height:{xs:'300px',sm:'400px',md:'450px'}, borderRadius:'10px'}}>
        <Box sx={{position:'absolute',left:0,width:'100%',height:'100%',background:'linear-gradient(90deg, rgba(0,0,0,0.6923144257703081) 0%, rgba(0,0,0,0) 48%)',zIndex:1}}></Box>
        {
            slidesInfo.map((slide, index) => {
                return(
                    <Box key={slide.url} sx={{transition:'.2s ease-in-out',position:'absolute',top:0,left:0,width:'100%', height:'100%',backgroundSize:'cover',backgroundRepeat:'no-repeat',backgroundImage:`url(${slide.url})`, opacity:activeIndex === index ? 1 : 0, borderRadius:'5px'}}></Box>
                )
            })
        }
        <Box sx={{position:'absolute',bottom:'20px',left:'20px',color:'white',maxWidth:'50%',zIndex:2, display:{xs:'flex',md:'block'}, flexDirection:'column'}}>
            <Typography mb='6px' variant='h3' sx={{fontSize:{xs:'2rem',md:'3rem'}}} fontWeight={{md:'bold'}}>{slidesInfo[activeIndex].price}</Typography>
            <Box sx={{width:'100px',height:{xs:'6px',md:'10px'},backgroundColor:'white', borderRadius:'30px'}}></Box>
            <Typography mt='6px' mb='1rem' fontWeight='600' sx={{fontSize:{xs:'15px',md:'1rem'}}}>{slidesInfo[activeIndex].description}</Typography>
            <Button variant='contained' sx={{backgroundColor:ButtonSecondary,'&:hover':{backgroundColor:SecondaryHover}}}><Typography p={{md:'4px'}} variant='h4' sx={{fontWeight:'bold', textTransform:'capitalize',fontSize:{xs:'1.5rem',md:'2rem', lineHeight:'35px'}}}>Buy</Typography></Button>
        </Box>
        <Box sx={{zIndex:3}}>
            <IconButton style={navigationBtnStyle} disableRipple variant='contained' sx={{left:'-20px'}} className="slider__btn" onClick={prevSlide}><NavigateBeforeIcon sx={{fontSize:'25px', color:'white'}}/></IconButton>
            <IconButton style={navigationBtnStyle} disableRipple variant='contained' sx={{right:'-20px'}} className="slider__btn" onClick={nextSlide}><NavigateNextIcon sx={{fontSize:'25px', color:'white'}}/></IconButton>
        </Box>
        <Box sx={{zIndex:3, position:'absolute',bottom:'-45px',right:'50%',transform:'translateX(50%)'}}>
            {
                slidesInfo.map((slide,index) => {
                        return(index != activeIndex ? <CircleOutlinedIcon key={slide.url} onClick={() => {setActiveIndex(index)}} sx={{color:SecondaryColor, marginRight:'10px', cursor:'pointer', fontSize:'20px'}} className="circle"/> : <CircleIcon key={index} sx={{color:SecondaryColor, marginRight:'10px', cursor:'pointer', fontSize:'20px'}}/>)
                })
            }
        </Box>
    </Stack>
    )
}
export default Slider;
/* the problem that I encountered here was with updating the info according to the appropriate slide. Based on my understanding, it was hard to do because of the delay of flickity load AND the access of the flickity DOM. After those being resolved with "window.onload" and useRef, I encountered another problem with combining ReactJs with VanillaJs. After accessing the selectedIndex, my next problem was to update that selectedIndex. I tried to update it with useEffect but I encoutnered problems because of the delay that flickity has. As a result, I found my way around by using doucment.readyState for StateHooks and conditions with normal variables.
After fixing this, I had a problem with going between pages, when I go back to the home page, all flickity data is gone and it acts like there are no data such as the selectedIndex thats why I used conditions "||" 0 but the counter will not change

Current Problems: Counter not changing / How to do it with dragging => Bringing the selectedIndex needs to be more dymanic.
    */